import { combineReducers } from 'redux';

// loginMessage holds the string that will display
// on the login screen if there's an error
const loginMessage = (state = '', action) => {
  switch (action.type) {
    case 'CLEAR_LOGIN_ERROR':
      return '';
    case 'LOGIN_INPUT_ERROR':
      return 'Please, enter your username and password.';
    case 'LOGIN_FAILED':
      return 'Oops! The username and password don\'t match. Please, try again.';
    case 'LOGIN_FAILED_NO_CODE':
      return 'Oops! Something went wrong with the server. Please, try again.';
    default:
      return state;
  }
};

// registrationMessage holds the string that will display
// on the registration screen if there's an error
const registrationMessage = (state = '', action) => {
  switch (action.type) {
    case 'CLEAR_REGISTRATION_ERROR':
      return '';
    case 'REGISTRATION_INPUT_ERROR':
      return 'Please, choose a username and password.';
    case 'REGISTRATION_FAILED':
      return 'Oops! Sorry, that username\'s already taken. Please, try again.';
    default:
      return state;
  }
};

// make one object that has keys loginMessage, registrationMessage
// these will be on the redux state at:
// state.errors.loginMessage and state.errors.registrationMessage
export default combineReducers({
  loginMessage,
  registrationMessage,
});
